import React, { useMemo } from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage, StaticImage } from "gatsby-plugin-image"
import styled from '@emotion/styled'
import { BgImage } from "gbimage-bridge"
import { useWindowSize } from 'react-use'

import Box from '../../../components/Box'
import { Media } from '../../../components/ThemeProvider/theme'

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const Banner = ({ onLoad = () => {} }) => {
  const { mobile, tablet, tablet_fat, desktop } = useStaticQuery(
    graphql`
      query {
        mobile: file(relativePath: { eq: "containers/HomePage/Banner/mobile.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
        tablet: file(relativePath: { eq: "containers/HomePage/Banner/tablet.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
        tablet_fat: file(relativePath: { eq: "containers/HomePage/Banner/tablet_fat.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
        desktop: file(relativePath: { eq: "containers/HomePage/Banner/desk.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
      }
    `
  )
  const imageMobile = getImage(mobile)
  const imageTablet = getImage(tablet)
  const imageTabletFat = getImage(tablet_fat)
  const imageDesk = getImage(desktop)
  const { width, height } = useWindowSize()
  const isFatScreen = useMemo(() => width > height, [height, width])
  return (
    <Box.Relative height="100%">
      <Box as={Media} lessThan="tablet" height="100%">
        <GatsbyBgImage image={imageMobile} onLoad={onLoad} />
        <Box.FullAbs>
          <StaticImage layout="fullWidth" src="mobile_text.png" alt="越永續，越得意" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} at="tablet" height="100%">
        {isFatScreen ? (
          <>
            <GatsbyBgImage image={imageTabletFat} onLoad={onLoad} />
            <Box.FullAbs>
              <StaticImage layout="fullWidth" src="tablet_text_fat.png" alt="越永續，越得意文字" />
            </Box.FullAbs>
          </>
        ) : (
          <>
            <GatsbyBgImage image={imageTablet} onLoad={onLoad} />
            <Box.FullAbs>
              <StaticImage layout="fullWidth" src="tablet_text.png" alt="越永續，越得意文字" />
            </Box.FullAbs>
          </>
        )}
      </Box>
      <Box as={Media} greaterThan="tablet" height="100%">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box>
    </Box.Relative>
  )
}

export default Banner
